@import url("https://fonts.googleapis.com/css2?family=League+Gothic&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Diplomata+SC&family=League+Gothic&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Diplomata+SC&family=Goldman&family=League+Gothic&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Diplomata+SC&family=Goldman&family=League+Gothic&family=Oxygen:wght@700&display=swap");

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.logo{
  width: 100px;
  margin: 0 15px 0 0;
}
.heading_style {
  /* text-align: center; */
  justify-content: center;
  display: flex;
  align-items: center;
  background-color: #B4131D;
  padding: 25px 0px 25px 0;
  margin: 0 0 0 0;
  
}
.base {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: #e5e7f4;
  padding: 25px 0 0 0;
}
.cards {
  margin: 0 60px 60px 60px;
  background-color: #ba0001;
  /* padding: 1px; */
  border: 1px solid;
  padding: 10px;
  box-shadow: rgb(0 0 0) 5px 10px 15px;
  /* position: relative; */
}
.cards:hover{
  box-shadow: rgb(50 50 50) 20px 20px 15px;
  /* position: absolute; */
margin-top: 10px;
  transition: 0.5s;
}
.card{

}
.card-info {
  font-family: "League Gothic", sans-serif;
  font-size: medium;
  text-align: center;
  padding: 0 0 25px 0;
  border-radius: 5px;
}
.card_category {
  color: gray;
  font-size: x-large;
}
.card_title {
  font-family: "Diplomata SC", cursive;
  max-width: 300px;
  text-align: center;
  justify-content: center;
  align-items: center;
}
.card-img {
  min-width: 300px;
  height: 400px;
}
button {
    font-family: "Oxygen", sans-serif;
  width: 150px;
  height: 35px;
  font-weight: 600;
  border-radius: 5px;
  background-color: #000;
  color: #e61f0b;
  border: none;
}
